import React, {  useState } from "react";
function Form(props) {

   const onRemove = (()=>{
     let newtext = text.replace(/\s+/g, " ") 
     SetText(newtext);
   })
   const onCopy = ()=>{
     var text = document.getElementById('text1');
     text.select();
     navigator.clipboard.writeText(text.value)
    props.stateAlert("Copied to clipboard")

   }
  const onChange = (event) => {
    SetText(event.target.value);
  };
  const onClear = () =>{
   
    SetText("");
    props.stateAlert("Clear!")
    
  };
  const onDisplay = () => {
    let newText = text.toUpperCase();
    SetText(newText);
    props.stateAlert("Converted to uppercase")
  };
  const onLowerCase = () => {
    let newText = text.toLowerCase();
    SetText(newText);
    props.stateAlert("Converted to lowercase")

  };
  const [text, SetText] = useState("");
  return (
    <div>
      <div className="mb-3">
        <label htmlFor="Text-Palet" className="form-label"></label>
        <div className="container">
          <h1>{props.title}</h1>
           
          <textarea
            id = "text1"
            className="form-control my-3 position-relative"
            placeholder="Enter your sentence"
           value =  {text}
            onChange={onChange}
            rows="8"
          ></textarea>
          <button className="btn btn-primary" onClick={onDisplay}>
            Convert to uppercase
          </button>
          <button className = "btn btn-primary mx-3" onClick = {onLowerCase}>Convert to lowercase</button>
          <button type="button" class="btn btn-primary " data-container="body" data-toggle="popovers" onClick = {onRemove} data-placement="top" data-content="Copied!">Remove extra spaces</button>
          <button type="button" class="btn btn-primary mx-3" data-container="body" data-toggle="popovers" onClick = {onCopy} data-placement="top" data-content="Copied!">
   Copy
</button>
          <button
            className="btn btn-primary  position-absolute"
            id="clear" onClick={onClear}
          >
            clear
          </button>
          <div className = "container">
          <h2 className = "my-3">Preview</h2>
          <p><span>No of words :{text.split(" ").length-1}  </span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<span>No of Characters : {text.length}</span></p>
          </div>
          
        </div>
      </div>
    </div>
  );
}

export default Form;
