import React from "react";

function Alerts(props) {
  return (
    props.message  && (
      <div>
        <div
          className="alert alert-warning alert-dismissible fade show"
          role="alert"
        >
          <strong>{props.message}</strong> 
          
         
        </div>
      </div>
    )
  );
}

export default Alerts;
