
import React from 'react'
import Form from './components/Form.js';
import Navbar from './components/Navbar.js';
import {useState} from 'react'
import Alerts from './components/Alerts.js';
function App() {

    const[alert,setAlert] = useState(null)
    const[mode,setMode] = useState('light')
      const stateAlert = ((message)=>{
          setAlert(message);
      })
    const toggle = ()=>{
      if(mode === 'dark'){
        setMode('light')
        stateAlert("Light-Mode has been enabled")
        

      }else{
        setMode('dark')
        stateAlert("Dark-Mode has been enabled")
      }
      setTimeout(()=>{
            setAlert(null);
      },100)
    }
    return (
     
     <div>
      <Navbar
       mode = {mode}
      toggleMode = {toggle}
      />
     <Alerts message={alert}/>

      <Form stateAlert = {stateAlert}/>
     </div>
    )
   }
 

export default App;